
* {
    box-sizing: border-box;
  }
  
  html, body {
    height: 100%;
  }
  
  body {
    margin: 0;
    padding: 0;
  }
  
  ul, li, a {
    list-style: none;
    text-decoration: none;
    padding: 0;
    margin: 0;
    color: inherit;
  }
  
  a, button {
    cursor: pointer;
  }